"use client";

import React, { useState, useEffect, useRef } from 'react';
import { Search, Star, ChevronRight, ChevronLeft, MapPin, Clock, GraduationCap } from 'lucide-react';
import Image from 'next/image';
import LandingNavBar from '@/components/landing/LandingNavBar';
import { programAPI } from '@/lib/api';
import { Program, ProgramQuery } from '@/types/Program';
import { useRouter } from 'next/navigation';

const ProgramsPage = () => {
    const [programs, setPrograms] = useState<Program[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [searchTerm, setSearchTerm] = useState('');
    const [filters, setFilters] = useState<ProgramQuery>({
        page: 1,
        limit: 12,
        sortBy: 'name',
        sortOrder: 'asc'
    });
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(1);
    const [totalPrograms, setTotalPrograms] = useState(0);
    const resultsRef = useRef<HTMLDivElement>(null);
    const router = useRouter();

    // Fetch programs from API
    const fetchPrograms = async (queryParams: ProgramQuery) => {
        try {
            setLoading(true);
            setError(null);
            
            console.log('Fetching programs with params:', queryParams);
            const response = await programAPI.getPrograms(queryParams);
            console.log('API Response:', response);
            
            if (response && response.success) {
                // Add null checks for response data
                const programsData = response.data?.programs || [];
                const paginationData = response.data?.pagination || {
                    page: 1,
                    limit: 12,
                    total: 0,
                    totalPages: 1
                };
                
                console.log('Programs data:', programsData);
                console.log('Pagination data:', paginationData);
                
                setPrograms(programsData);
                setTotalPages(paginationData.totalPages || 1);
                setTotalPrograms(paginationData.total || 0);
                setCurrentPage(paginationData.page || 1);
            } else {
                console.error('API response not successful:', response);
                setError(response?.message || 'Failed to fetch programs');
                // Set default values when there's an error
                setPrograms([]);
                setTotalPages(1);
                setTotalPrograms(0);
                setCurrentPage(1);
            }
        } catch (err: any) {
            console.error('Error fetching programs:', err);
            setError(err.response?.data?.message || 'Failed to fetch programs');
            // Set default values when there's an error
            setPrograms([]);
            setTotalPages(1);
            setTotalPrograms(0);
            setCurrentPage(1);
        } finally {
            setLoading(false);
        }
    };

    // Initial load
    useEffect(() => {
        fetchPrograms(filters);
    }, []);

    // Handle search and filters
    useEffect(() => {
        console.log('🔄 Filter effect triggered with:', { searchTerm, filters });

        // Ensure school filter is always a string (code, name, or _id)
        let schoolValue = filters.school;
        if (
            schoolValue &&
            typeof schoolValue === 'object' &&
            (('code' in schoolValue) || ('_id' in schoolValue) || ('name' in schoolValue))
        ) {
            // Ưu tiên code, nếu không có thì _id, nếu không có thì name
            schoolValue = (schoolValue as any).code || (schoolValue as any)._id || (schoolValue as any).name;
        }

        const queryParams: ProgramQuery = {
            ...filters,
            school: schoolValue,
            page: 1, // Reset to first page when filters change
            search: searchTerm || undefined
        };

        console.log('📤 Query params before cleanup:', queryParams);

        // Remove empty filters
        Object.keys(queryParams).forEach(key => {
            if (queryParams[key as keyof ProgramQuery] === '' || queryParams[key as keyof ProgramQuery] === undefined) {
                console.log(`🗑️ Removing empty filter: ${key}`);
                delete queryParams[key as keyof ProgramQuery];
            }
        });

        console.log('📤 Final query params to send:', queryParams);
        fetchPrograms(queryParams);
    }, [searchTerm, filters.school, filters.credential, filters.delivery, filters.sortBy, filters.sortOrder]);

    const handlePageChange = (pageNumber: number) => {
        const queryParams: ProgramQuery = {
            ...filters,
            page: pageNumber,
            search: searchTerm || undefined
        };
        
        fetchPrograms(queryParams);
        
        // Scroll to top of results
        resultsRef.current?.scrollIntoView({ 
            behavior: 'smooth',
            block: 'start'
        });
    };

    const handleSearchKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            resultsRef.current?.scrollIntoView({ 
                behavior: 'smooth',
                block: 'start'
            });
        }
    };

    const handleFilterClick = (filter: string, filterType: 'school' | 'credential' | 'delivery') => {
        console.log('🔍 Filter clicked:', { filter, filterType });
        console.log('📊 Current filters before update:', filters);
        
        setFilters(prev => {
            const newFilters = {
                ...prev,
                [filterType]: prev[filterType] === filter ? undefined : filter,
                page: 1 // Reset to first page
            };
            console.log('📊 New filters after update:', newFilters);
            return newFilters;
        });
    };

    const clearAllFilters = () => {
        console.log('🧹 Clearing all filters');
        setFilters({
            page: 1,
            limit: 12,
            sortBy: 'name',
            sortOrder: 'asc'
        });
        setSearchTerm('');
    };

    const schoolFilters = [
        "Seneca Business",
        "School of Creative Arts & Animation", 
        "School of Public Safety",
        "School of Arts & Science",
        "School of Software Design & Data Science",
        "School of Aviation",
        "School of Health & Wellness",
        "School of Hospitality & Tourism",
        "School of Media & Communications"
    ];

    const credentialFilters = [
        "Ontario College Certificate",
        "Ontario College Diploma", 
        "Ontario College Advanced Diploma",
        "Ontario College Graduate Certificate",
        "Bachelor of Business Administration",
        "Seneca Academic and Career Entrance (ACE) certificate"
    ];

    const deliveryFilters = [
        "In-person",
        "Online", 
        "Hybrid",
        "Blended"
    ];

    const sortOptions = [
        { label: "Name A-Z", value: "name", order: "asc" },
        { label: "Name Z-A", value: "name", order: "desc" },
        { label: "Duration", value: "duration", order: "asc" },
        { label: "Code", value: "code", order: "asc" }
    ];

    if (loading && (!programs || programs.length === 0)) {
        return (
            <div className="min-h-screen bg-[#1a1a1a] text-white">
                <div className="sticky top-0 z-30 bg-[#18191A]">
                    <LandingNavBar />
                </div>
                <div className="h-20" />
                <div className="border-b border-white/10 w-full" />
                <div className="w-full border-b border-[#232425] min-h-screen flex items-center justify-center">
                    <div className="max-w-6xl mx-auto px-6 py-30 text-center">
                        <div className="animate-pulse">
                            <div className="h-16 bg-gray-700 rounded w-1/3 mb-6 mx-auto"></div>
                            <div className="h-6 bg-gray-700 rounded w-1/2 mb-15 mx-auto"></div>
                            <div className="h-12 bg-gray-700 rounded w-1/3 mb-10 mx-auto"></div>
                            <div className="flex flex-wrap gap-4 mb-4 justify-center">
                                {[1, 2, 3, 4, 5].map(i => (
                                    <div key={i} className="h-12 bg-gray-700 rounded w-32"></div>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-[#1a1a1a] text-white">
            <div className="sticky top-0 z-30 bg-[#18191A]">
                <LandingNavBar />
            </div>
            <div className="h-20" />
            <div className="border-b border-white/10 w-full" />
            
            {/* Header */}
            <div className="w-full border-b border-[#232425] min-h-screen flex items-center justify-center">
                <div className="max-w-6xl mx-auto px-6 py-30 text-center">
                    <h1 className="text-8xl font-bold mb-5">Programs</h1>
                    <p className="text-gray-300 mb-15 max-w-full mx-auto text-lg">
                        Explore our comprehensive range of programs designed to prepare you for success in your chosen field.
                    </p>
                    
                    {/* Search Bar */}
                    <div className="relative w-full max-w-2xl mb-10 mx-auto">
                        <Search className="absolute left-4 top-1/2 -translate-y-1/2 text-gray-400 w-5 h-5" />
                        <input
                            type="text"
                            value={searchTerm}
                            onChange={e => setSearchTerm(e.target.value)}
                            placeholder="Search for programs..."
                            className="w-full pl-12 pr-4 py-5 rounded-full bg-[#232425] text-white placeholder-gray-400 focus:ring-2 focus:ring-amber-50 outline-none transition cursor-text"
                            onKeyDown={handleSearchKeyDown}
                        />
                    </div>

                    {/* School Filters */}
                    <div className="mb-6">
                        <h3 className="text-lg font-semibold mb-3 text-gray-300">Filter by School</h3>
                        <div className="flex flex-wrap gap-3 justify-center">
                            {schoolFilters.map((filter) => (
                                <button
                                    key={filter}
                                    className={`px-4 py-2 rounded-lg cursor-pointer font-medium transition-all duration-300 transform hover:scale-105
                                        ${filters.school === filter
                                            ? "bg-gradient-to-r from-orange-500 to-red-500 text-white shadow-orange-500/50 shadow-xl"
                                            : "bg-gradient-to-r from-gray-800/80 to-gray-700/80 text-gray-200 hover:from-orange-600/20 hover:to-red-600/20 hover:text-white border border-gray-600/50 hover:border-orange-400/50"
                                        }
                                    `}
                                    onClick={() => handleFilterClick(filter, 'school')}
                                >
                                    {filter}
                                </button>
                            ))}
                        </div>
                    </div>

                    {/* Credential Filters */}
                    <div className="mb-6">
                        <h3 className="text-lg font-semibold mb-3 text-gray-300">Filter by Credential</h3>
                        <div className="flex flex-wrap gap-3 justify-center">
                            {credentialFilters.map((filter) => (
                                <button
                                    key={filter}
                                    className={`px-4 py-2 rounded-lg cursor-pointer font-medium transition-all duration-300 transform hover:scale-105
                                        ${filters.credential === filter
                                            ? "bg-gradient-to-r from-orange-500 to-red-500 text-white shadow-orange-500/50 shadow-xl"
                                            : "bg-gradient-to-r from-gray-800/80 to-gray-700/80 text-gray-200 hover:from-orange-600/20 hover:to-red-600/20 hover:text-white border border-gray-600/50 hover:border-orange-400/50"
                                        }
                                    `}
                                    onClick={() => handleFilterClick(filter, 'credential')}
                                >
                                    {filter}
                                </button>
                            ))}
                        </div>
                    </div>

                    {/* Clear Filters */}
                    {(filters.school || filters.credential || filters.delivery || searchTerm) && (
                        <button
                            onClick={clearAllFilters}
                            className="px-6 py-2 bg-gray-600 hover:bg-gray-500 text-white rounded-lg transition-colors"
                        >
                            Clear All Filters
                        </button>
                    )}
                </div>
            </div>

            {/* Programs List */}
            <div className="max-w-6xl mx-auto px-6 pb-20 pt-10" ref={resultsRef}>
                {/* Results Header */}
                <div className="flex justify-between items-center mb-8">
                    <div>
                        <h2 className="text-2xl font-bold mb-2">
                            {error ? 'Error Loading Programs' : 'Available Programs'}
                        </h2>
                        <p className="text-gray-400">
                            {error ? error : `Showing ${programs?.length || 0} of ${totalPrograms} programs`}
                        </p>
                    </div>

                    {/* Sort Options */}
                    <div className="flex items-center space-x-4">
                        <span className="text-gray-300">Sort by:</span>
                        <select
                            value={`${filters.sortBy}-${filters.sortOrder}`}
                            onChange={(e) => {
                                const [sortBy, sortOrder] = e.target.value.split('-');
                                setFilters(prev => ({ ...prev, sortBy, sortOrder: sortOrder as 'asc' | 'desc' }));
                            }}
                            className="bg-[#232425] text-white px-4 py-2 rounded-lg border border-gray-600 focus:ring-2 focus:ring-orange-500 outline-none"
                        >
                            {sortOptions.map(opt => (
                                <option key={`${opt.value}-${opt.order}`} value={`${opt.value}-${opt.order}`}>
                                    {opt.label}
                                </option>
                            ))}
                        </select>
                    </div>
                </div>

                {/* Error State */}
                {error && (
                    <div className="text-center py-20">
                        <p className="text-red-400 text-lg mb-4">{error}</p>
                        <button
                            onClick={() => fetchPrograms(filters)}
                            className="px-6 py-2 bg-orange-500 hover:bg-orange-600 text-white rounded-lg transition-colors"
                        >
                            Try Again
                        </button>
                    </div>
                )}

                {/* Programs Grid */}
                {!error && programs && programs.length > 0 && (
                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
                        {programs?.map((program) => (
                            <div
                                key={program.id}
                                className="bg-[#232425] rounded-xl overflow-hidden shadow-lg transition-transform duration-300 ease-in-out hover:shadow-2xl hover:scale-105 cursor-pointer border border-gray-700 hover:border-orange-500"
                                onClick={() => router.push(`/programs/${program.id}`)}
                            >
                                {/* Program Header */}
                                <div className="p-6">
                                    <div className="flex items-start justify-between mb-4">
                                        <div className="flex-1">
                                            <h3 className="text-xl font-bold text-white mb-2 line-clamp-2">
                                                {program.name || 'Unnamed Program'}
                                            </h3>
                                            <p className="text-orange-400 font-semibold text-sm mb-2">
                                                {program.code || 'N/A'}
                                            </p>
                                        </div>
                                    </div>

                                    {/* Program Details */}
                                    <div className="space-y-3 mb-4">
                                        <div className="flex items-center text-gray-300 text-sm">
                                            <GraduationCap className="w-4 h-4 mr-2 text-orange-400" />
                                            <span className="line-clamp-1">{program.credential || 'N/A'}</span>
                                        </div>
                                        
                                        <div className="flex items-center text-gray-300 text-sm">
                                            <Clock className="w-4 h-4 mr-2 text-orange-400" />
                                            <span>{program.duration || 'N/A'}</span>
                                        </div>

                                        {program.campus && program.campus.length > 0 && (
                                            <div className="flex items-center text-gray-300 text-sm">
                                                <MapPin className="w-4 h-4 mr-2 text-orange-400" />
                                                <span className="line-clamp-1">
                                                    {program.campus.join(', ')}
                                                </span>
                                            </div>
                                        )}
                                    </div>

                                    {/* School */}
                                    <div className="mb-4">
                                        <p className="text-gray-400 text-sm font-medium">
                                            {program.school || 'N/A'}
                                        </p>
                                    </div>

                                    {/* Overview */}
                                    {program.overview && (
                                        <p className="text-gray-300 text-sm line-clamp-3 mb-4">
                                            {program.overview}
                                        </p>
                                    )}

                                    {/* View Details Button */}
                                    <div className="flex justify-end">
                                        <button className="text-orange-400 hover:text-orange-300 text-sm font-medium transition-colors">
                                            View Details →
                                        </button>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                )}

                {/* No Results */}
                {!error && !loading && (!programs || programs.length === 0) && (
                    <div className="text-center py-20">
                        <p className="text-gray-400 text-lg mb-4">
                            No programs found matching your criteria
                        </p>
                        <button
                            onClick={clearAllFilters}
                            className="px-6 py-2 bg-orange-500 hover:bg-orange-600 text-white rounded-lg transition-colors"
                        >
                            Clear Filters
                        </button>
                    </div>
                )}

                {/* Pagination */}
                {totalPages > 1 && (
                    <div className="flex justify-center items-center space-x-4 mt-8">
                        <button
                            onClick={() => handlePageChange(currentPage - 1)}
                            disabled={currentPage === 1}
                            className="px-4 py-2 rounded-lg bg-[#36454F] text-white disabled:opacity-50 disabled:cursor-not-allowed hover:bg-[#2a3439] transition-colors"
                        >
                            <ChevronLeft className="w-4 h-4" />
                        </button>
                        
                        <div className="flex items-center space-x-2">
                            {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                                const pageNum = i + 1;
                                return (
                                    <button
                                        key={pageNum}
                                        onClick={() => handlePageChange(pageNum)}
                                        className={`px-3 py-2 rounded-lg transition-colors ${
                                            currentPage === pageNum
                                                ? "bg-orange-500 text-white"
                                                : "bg-[#36454F] text-gray-300 hover:bg-[#2a3439]"
                                        }`}
                                    >
                                        {pageNum}
                                    </button>
                                );
                            })}
                        </div>
                        
                        <button
                            onClick={() => handlePageChange(currentPage + 1)}
                            disabled={currentPage === totalPages}
                            className="px-4 py-2 rounded-lg bg-[#36454F] text-white disabled:opacity-50 disabled:cursor-not-allowed hover:bg-[#2a3439] transition-colors"
                        >
                            <ChevronRight className="w-4 h-4" />
                        </button>
                    </div>
                )}
            </div>
        </div>
    );
};

export default ProgramsPage; 

